<template>
    <div>
        <van-nav-bar title="评论" fixed left-arrow @click-left="$router.back()" />
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="main">
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <van-cell class="main" v-for="obj, index in list" :key="index">
                    <img :src='obj.user.avatarUrl'>
                    <span>{{ obj.user.nickname }}</span>&nbsp;
                    <span>{{ obj.time }}</span>
                    <div>{{ obj.likedCount }}点赞</div>
                    <div>{{ obj.content }}</div>
                </van-cell>
                <div><span>1111</span></div>
                <div><span>1111</span></div>
                <div><span>1111</span></div>
                <div><span>1111</span></div>
                <div><span>1111</span></div>
                <div><span>1111</span></div>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>
import { getCommentListAPI } from '@/api'
export default {
    data() {
        return {
            list: [],
            isLoading: false,
            loading: false,//底部控制加载状态
            finished: false,
            page: 0
        }
    },
    methods: {
        async getList() {
            let res = await getCommentListAPI({
                id: this.$route.query.id,
                limit: 20,
                offset: (this.page - 1) * 20
            })
            res.data.comments.forEach(obj=>this.list.push(obj))
            this.isLoading = false;
            this.loading = false
        },
        onRefresh() {
            this.list = []
            this.page=1
            this.getList()
        },
        onLoad() {
            this.page++
            this.getList()
            this.loading = false
            this.finished = true
        }
    },
}
</script>

<style scoped>
.main {
    margin-top: 50px;
}
</style>